<template>
  <div class="curriculum">
    <div class="search-form">
      <Form ref="formValidate" :model="searchData" inline :label-width="40">
        <FormItem label="日期" prop="startTime" >
          <DatePicker :clearable="false" v-model="searchData.startTime" type="date" placeholder="开始时间" style="width: 200px"></DatePicker>
        </FormItem>
        <FormItem label="—" class="date-line" prop="endTime" :label-width="20">
          <DatePicker :clearable="false" v-model="searchData.endTime" type="date" placeholder="结束时间" style="width: 200px"></DatePicker>
        </FormItem>
        <FormItem :label-width="20">
            <Button type="primary" @click="search()">查询</Button>
            <Button @click="reset()" style="margin-left:8px">重置</Button>
            <Button type="primary" icon="ios-cloud-upload-outline" style="margin-left:8px" @click="exportTable()">导出</Button>
            <Button type="success" icon="md-add-circle" style="margin-left:8px" @click="batchAdd()">添加</Button>
        </FormItem>
      </Form>
    </div>
     <div class="table-part">
       <Table :row-class-name="rowClassName" height="760" ref="table" stripe border :columns="columns" :data="data"></Table>
     </div>
      <Modal :width="800" v-model="addModal" :title="edit?'编辑':'添加'" @on-ok="addModalOk" @on-cancel="addModalCancel">
      <div>
        <Form ref="addFormData" inline :model="addFormData" :rules="addFormRule" :label-width="90">
          <FormItem label="姓名" prop="studentId">
            <Select :disabled="edit" style="width: 160px" v-model="addFormData.studentId" placeholder="学生姓名">
              <Option v-for="(label,value) in studentsMap" :key="value" :value="value">{{label}}</Option>
            </Select>
          </FormItem>
          <FormItem label="课时数" prop="count">
            <InputNumber style="width: 160px" :editable="false" :max="10" :min="1" v-model="addFormData.count"></InputNumber>
          </FormItem>
          <FormItem v-if="edit" label="是否可删除" prop="count">
            <i-switch  v-model="isDelectPast">
              <span slot="open">是</span>
              <span slot="close">否</span>
            </i-switch>
          </FormItem>
           <FormItem label="日期" prop="date">
            <DatePicker :clearable="false" v-model="addFormData.date" type="date" placeholder="日期" style="width: 160px"></DatePicker>
          </FormItem>
          <FormItem label="开始时间" prop="startTime">
            <Select style="width: 160px" v-model="addFormData.startTime" placeholder="时间">
              <Option v-for="time in dealScheduleData" :key="time" :value="time">{{time}}</Option>
            </Select>
          </FormItem>
          <FormItem :label-width="20">
              <Button type="primary" @click="addTable()">添加</Button>
          </FormItem>
        </Form>
      </div>
      <div class="table-part">
        <Table :columns="addFormColumns" :data="dealAddTableData"></Table>
      </div>
      <div slot="footer">
        <Button @click="addModalCancel()" style="margin-right: 10px">取消</Button>
        <Button type="primary" v-if="edit" @click="editModalOk()">编辑</Button>
        <Button type="primary" v-else @click="addModalOk()">确定</Button>
      </div>
    </Modal>
  </div>
</template>

<script>
import Vue from 'vue'
import { times } from '../../utils/times'
import time from '../../mixins/time'
export default {
  mixins: [time],
  data () {
    const ratherDate = function (date) {
      let today = Vue.lib.myMoment().formate('YYYY-MM-DD')
      return new Date(today) - new Date(date) > 0
    }
    return {
      scheduleData: times,
      dateMap: {},
      searchData: {
        startTime: '',
        endTime: '',
        name: ''
      },
      data: [],
      columns: [],
      defaultColumn: [ {
        title: '时间',
        align: 'center',
        key: 'date',
        fixed: 'left',
        width: 70
      }, {
        title: '星期',
        align: 'center',
        key: 'week',
        fixed: 'left',
        width: 50
      }],
      studentsMap: {},
      addModal: false,
      addFormData: {
        studentId: '',
        startTime: '',
        date: '',
        count: 3
      },
      addFormRule: {
        studentId: [
          { required: true, message: '请选择学生姓名', trigger: 'change' }
        ],
        date: [
          { required: true, type: 'date', message: '请选择上课日期', trigger: 'change' }
        ],
        startTime: [
          { required: true, message: '请选择开始时间', trigger: 'change' }
        ]
      },
      addFormColumns: [ {
        title: '姓名',
        key: 'studentName',
        align: 'center'
      }, {
        title: '日期',
        key: 'date',
        align: 'center'
      }, {
        title: '小时数',
        key: 'count',
        align: 'center'
      }, {
        title: '时间',
        key: 'period',
        align: 'center',
        width: 140
      }, {
        title: '操作',
        key: 'action',
        width: 200,
        align: 'center',
        render: (h, params) => {
          return h('a', {
            attrs: {
              class: ratherDate(params.row.date) ? 'a-disabled' : ''
            },
            on: {
              click: () => {
                if (!this.isDelectPast && ratherDate(params.row.date)) {
                  this.$Message.info('不可删除今天之前的课程')
                  return
                }
                this.delectTableByDate(params.row)
              }
            }
          }, '删除')
        }
      }],
      addTableData: [],
      editModal: false,
      searchStudent: '',
      selectCol: {
        studentName: '',
        studentId: '',
        time: '',
        date: '',
        id: ''
      },
      edit: false,
      delectCache: [],
      isDelectPast: false
    }
  },
  computed: {
    dealScheduleData () {
      return this.scheduleData.filter((item, i) => {
        return this.scheduleData.length - i >= this.addFormData.count
      })
    },
    dealAddTableData () {
      return this.dealAllData(this.addTableData)
    }
  },
  created () {
    this.initDate()
    this.initTable()
    this.$indexedDB.initStore('curriculum', this.query)
  },
  methods: {
    search () {
      this.initTable()
      this.query()
    },
    reset () {
      this.initDate()
      this.query()
    },
    exportTable () {
      let { startTime, endTime } = this.setTime()
      this.$refs.table.exportCsv({
        filename: `${startTime}-${endTime}课时统计`,
        data: this.data.map(item => {
          for (const key in item) {
            if (key !== 'date' && key !== 'week' && item[key]) {
              item[key] = this.studentsMap[item[key]]
            }
          }
          return item
        })
      })
      this.query()
    },
    batchAdd () {
      this.edit = false
      this.addModal = true
    },
    addTable () {
      this.$refs.addFormData.validate((valid) => {
        if (valid) {
          let{ studentId, startTime, date, count } = this.addFormData
          let index = this.scheduleData.indexOf(startTime)
          var times = this.scheduleData.slice(index, index + count)
          let data = { }
          data.date = this.$lib.myMoment(new Date(date)).formate('YYYY-MM-DD')
          data.studentId = studentId
          data.studentName = this.studentsMap[studentId]

          for (const table of this.addTableData) {
            if (data.date === table.date && times.includes(table.time)) {
              this.$Message.info(`${table.time}时间已有安排，请重新选择`)
              return
            }
          }
          for (const time of times) {
            this.addTableData.push({ ...data, time })
          }
        }
      })
    },
    delectTableByDate (row) {
      this.addTableData = this.addTableData.filter(item => {
        if (item.studentId === row.studentId) {
          let f = row.date !== item.date
          if (!f && item.id) {
            this.delectCache.push(item.id)
          }
          return f
        }
        return true
      })
    },
    async addModalOk () {
      if (this.addTableData.length === 0) {
        this.$Message.info('未排课')
        return
      }
      for (const add of this.addTableData) {
        if (!this.validateRepeat(add)) return
      }
      let count = 0
      for (const add of this.addTableData) {
        count += await this.addPost(add)
      }
      this.$Message.success(`成功添加${count}条记录`)
      this.addModalCancel()
      this.query()
    },
    async editModalOk () {
      let addCount = 0
      let delectCount = 0
      for (const add of this.addTableData) {
        if (!add.id && !this.validateRepeat(add)) { return }
      }
      for (const all of this.allData) {
        if (this.delectCache.includes(all.id)) {
          delectCount += await this.delectPost(all)
        }
      }
      for (const add of this.addTableData) {
        if (!add.id) {
          addCount += await this.addPost(add)
        }
      }
      this.$Message.success(`成功添加${addCount}条记录,删除了${delectCount}条记录`)
      this.addModalCancel()
      this.query()
    },
    addModalCancel () {
      this.addTableData = []
      this.$refs.addFormData.resetFields()
      this.addModal = false
    },
    delectPost (data) {
      data.isDelect = true
      return this.$indexedDB.http({
        method: 'put',
        url: 'curriculum',
        data: data
      }).then((res) => {
        return 1
      })
    },
    addPost (data) {
      return this.$indexedDB.http({
        method: 'add',
        url: 'curriculum',
        data: data
      }).then((res) => {
        return 1
      })
    },
    query () {
      this.setData()
      this.$indexedDB.http({
        method: 'get',
        url: 'curriculum'
      }).then((res) => {
        this.allData = res.data.filter(item => {
          return !item.isDelect
        })
        for (const r of this.allData) {
          let k = this.dealTimeKey(r.time)
          for (const d of this.data) {
            if (r.date === d.date) {
              this.$set(d, k, r.studentId)
            }
          }
        }
      })
    },
    initDate () {
      this.searchData.startTime = this.$lib.showWeekFirstDay()
      this.searchData.endTime = this.getDefaultEnd(13)
      this.searchData.name = ''
    },
    initTable () {
      this.setColums()
      this.setData()
    },
    setData () {
      let { startTime, endTime } = this.setTime()
      let dates = this.$lib.getAll(startTime, endTime)
      this.data = []
      for (const d of dates) {
        let w = this.$lib.myMoment(d).formate('ddd')
        this.data.push({
          date: `${d}`,
          week: w
        })
      }
    },
    setColums () {
      this.columns = []
      this.columns = this.columns.concat(this.defaultColumn)
      let self = this
      for (const s of this.scheduleData) {
        let k = this.dealTimeKey(s)
        this.columns.push({
          title: s,
          key: k,
          align: 'center',
          minWidth: 55,
          render (h, params) {
            return h('div', {
              attrs: {
                class: params.row[k] ? 'cell-has-name name-part' : 'name-part'
              },
              on: {
                click () {
                  if (!params.row[k]) {
                    self.$Message.info('该时间段无学生')
                    return
                  }
                  self.delectCache = []
                  self.addFormData.studentId = params.row[k]
                  self.addTableData = self.allData.filter(all => {
                    return all.studentId === self.addFormData.studentId
                  })
                  self.addModal = true
                  self.edit = true
                }
              }
            }, params.row[k] ? self.studentsMap[params.row[k]] : '')
          }
        })
      }
    },
    dealTimeKey (time) {
      let chart = ['a', 'b', 'c', 'd', 'e', 'f', 'g', 'h', 'i', 'j', 'k', 'l', 'm', 'n', 'o', 'p', 'q', 'r', 's', 't', 'u', 'v', 'w', 'x', 'y', 'z', 'aa', 'bb', 'cc', 'dd', 'ee', 'ff', 'gg', 'hh', 'ii', 'jj', 'kk', 'll', 'mm', 'nn']
      return chart[this.scheduleData.indexOf(time)]
    },
    validateRepeat (add) {
      for (const all of this.allData) {
        if (add.date === all.date && add.time === all.time) {
          this.$Message.info(`${all.date} ${all.time}已有安排学生：${all.studentName}`)
          return false
        }
      }
      return true
    },
    setTime () {
      let { startTime, endTime } = this.searchData
      startTime = this.$lib.myMoment(new Date(startTime)).formate('YYYY-MM-DD')
      endTime = this.$lib.myMoment(new Date(endTime)).formate('YYYY-MM-DD')
      return { startTime, endTime }
    },
    getDefaultEnd (c) {
      let { startTime } = this.searchData
      return new Date(new Date(startTime).getTime() + (86400000 * c))
    },
    rowClassName (row, index) {
      let weekNum = new Date(row.date).getDay()
      let today = this.$lib.myMoment(new Date()).formate('YYYY-MM-DD')
      if (row.date === today) {
        return 'is-today'
      }
      if (weekNum === 0 || weekNum === 6) {
        return 'weekend-heightlight'
      }
      return ''
    }
  }
}
</script>

<style>
.ivu-table .demo-table-info-row td{
    background-color: #2db7f5;
    color: #fff;
}
.ivu-table .demo-table-error-row td{
    background-color: #ff6600;
    color: #fff;
}
</style>
